$active-stars: 5;

.stars {
  display: flex;
  gap: 4px;

  &__star {
    width: 16px;
    height: 16px;
    background-image: url('../images/star.svg');
    background-repeat: no-repeat;
    background-position: center;
  }

  @for $active-stars from 1 through 5 {
    &--#{$active-stars} > :nth-child(-n + #{$active-stars}) {
      background-image: url('../images/star-active.svg');
    }
  }
}
