html {
  font-family: Roboto, Montserrat, sans-serif;
}

body {
  margin: 0;
}

img {
  display: block;
}

.header {
  justify-content: space-between;
  align-items: center;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 50px;
  display: flex;
}

.menu__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.menu__item:not(:last-child) {
  margin-right: 20px;
}

.menu__link {
  text-transform: uppercase;
  color: #060b35;
  text-align: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  transition: color .5s;
  display: inline-block;
  position: relative;
}

.menu__link.is-active {
  color: #00acdc;
}

.menu .is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.menu__link:hover {
  color: #00acdc;
}

.main {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  max-width: 1024px;
  margin: 0 auto;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .main {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .main {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .main {
    grid-template-columns: repeat(4, 200px);
    padding: 50px 128px;
  }
}

.card {
  box-sizing: border-box;
  color: #616070;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
  font-size: 10px;
  line-height: 14px;
  transition: transform .5s;
  transform: scale(1);
}

.card__img {
  margin-bottom: 40px;
  padding: 0 3px;
}

.card__title {
  color: #060b35;
  margin: 0 0 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  margin: 0 0 16px;
}

.card__evaluation {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  display: flex;
}

.card__reviews {
  color: #060b35;
  margin: 0;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  display: flex;
}

.card__price-title {
  margin: 0;
  font-size: 12px;
  line-height: 18px;
}

.card__price-value {
  color: #060b35;
  margin: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__btn {
  box-sizing: border-box;
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #0000;
  border-radius: 5px;
  padding: 12px 70px;
  font-size: 14px;
  font-weight: 700;
  line-height: 14px;
  text-decoration: none;
  transition: color .5s, background-color .5s, border-color .5s;
  display: block;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__title {
  color: #34568b;
}

.card:hover .card__btn {
  color: #00acdc;
  background-color: #fff;
  border-color: #00acdc;
}

.stars {
  gap: 4px;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars--1 > :nth-child(-n+1), .stars--2 > :nth-child(-n+2), .stars--3 > :nth-child(-n+3), .stars--4 > :nth-child(-n+4), .stars--5 > :nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

